.track-preview.list {
  display: flex;
  column-gap: 0.5rem;
  cursor: pointer;
  img {
    width: 4rem;
    height: 4rem;
  }

  .track-details {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    height: 4rem;
    line-height: 1.125rem;

    > * {
      margin-bottom: 0;
    }

    .track-artist {
      font-size: 0.75rem;
      color: $clr4;
    }

    .track-info {
      display: flex;
      column-gap: 0.5rem;
      font-size: 0.75rem;
      color: $clr4;

      .play-count::before {
        content: '\f04b';
        font-family: Awesome;
        margin-right: 0.5rem;
      }

      .audio-length::before {
        content: '\f017';
        font-family: Awesome;
        margin-right: 0.5rem;
      }

      .created-time::before {
        content: '\f073';
        font-family: Awesome;
        margin-right: 0.5rem;
      }
    }
  }
}

