.search {
  position: relative;
  margin-bottom: 2rem;
  width: 100%;
  height: 60px;
  background-color: white;
  border-radius: 60px;
  box-shadow: 0 0 0 5px $clr2;
  transition: 0.5s;
  overflow: hidden;

  .icon {
    position: absolute;
    top: 0;
    left: 0;
    width: 60px;
    height: 60px;
    background-color: white;
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 1;
    cursor: pointer;

    &::before {
      content: '';
      position: absolute;
      width: 15px;
      height: 15px;
      border: 3px solid $clr2;
      border-radius: 50%;
      transform: translate(-4px, -4px);
    }
    &::after {
      content: '';
      position: absolute;
      width: 3px;
      height: 12px;
      background: $clr2;
      transform: translate(6px, 6px) rotate(315deg);
    }
  }

  &.active {
    width: 100%;
  }

  .search-bar {
    position: relative;
    width: 100%;
    height: 60px;
    left: 60px;
    display: flex;
    justify-content: center;
    align-items: center;

    input {
      position: absolute;
      top: 0;
      width: 100%;
      height: 100%;
      border: none;
      outline: none;
      font-size: 1.25rem;
      padding: 10px 0;
    }
  }

  .clear {
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    width: 15px;
    height: 15px;
    right: 15px;
    cursor: pointer;
    display: flex;
    justify-content: center;
    align-items: center;

    &::before {
      position: absolute;
      content: '';
      width: 1px;
      height: 15px;
      background: #999;
      transform: rotate(315deg);
    }

    &::after {
      position: absolute;
      content: '';
      width: 1px;
      height: 15px;
      background: #999;
      transform: rotate(45deg);
    }
  }
}
